import Vue from 'vue'
import Router from 'vue-router'
import Display from '@/pages/Display'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/ConnDisplay',
      name: 'ConnDisplay',
      component: Display
    },
    { path: '*', redirect: '/ConnDisplay' }
  ]
})



